img {
    max-width: 100%;
  }
  .col {
    display: flex;
    justify-content: space-between;
    gap: 5%;
  }
  .col + .col {
    margin-top: 2rem;
  }
  
  .colImg {
    background-color: hsl(15, 100%, 70%);
    height: 65px;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    position: relative;
    overflow: hidden;
  }
  .colImg img {
    position: absolute;
    right: 18px;
    top: -12px;
  }
  .work {
    border-radius: 0.5rem;
  }
  .work__time {
    position: relative;
    background-color: hsl(235, 46%, 20%);
    border-radius: 0.5rem;
    padding: 27px 30px;
    width: 13rem;
    margin-top: -20px;
    color: white;
  }
  .work__time:hover {
    background-color: hsl(235, 45%, 61%);
  }
  .work__option {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  .work__option img {
    cursor: pointer;
  }
  .work__hrs {
    font-weight: 300;
    margin-bottom: 20px;
    font-size: 2rem;
  }
