* {
    margin: 0;
    box-sizing: border-box;
  }
  @import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&family=Rubik:wght@300;400;500&display=swap");
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: hsl(226, 43%, 10%);
    padding: 230px 0px;
    color: white;
    font-family: "Rubik", sans-serif;
  }
  .wrapper {
    width: 90%;
    display: flex;
    justify-content: center;
    gap: 2rem;
  }
  .NameCard {
    width: 20%;
    border-radius: 0.5rem;
    background-color: hsl(235, 46%, 20%);
  }
  .reportCard {
    background-color: hsl(246, 80%, 60%);
    border-radius: 0.5rem;
    padding: 3.5rem 2.1rem;
  }
  .reportCard img {
    width: 40%;
    border: 0.3rem solid white;
    border-radius: 100px;
    margin-bottom: 1rem;
  }
  .reportCard h1 {
    font-size: 2.7rem;
  }
  
  .timeInterval {
    list-style: none;
    text-decoration: none;
  }
  .clicked {
    text-decoration: none;
    color: white;
  }
  .unClicked {
    text-decoration: none;
    color: hsl(246, 80%, 60%);
  }
  .timeInterval li {
    padding: 0.7rem 0rem;
  }
  
  @media (max-width: 480px) {
    .reportCard {
      display: flex;
      padding: 1.2rem 1.1rem;
      justify-content: space-evenly;
      align-items: center;
    }
    .NameCard {
      width: 90%;
    }
    .wrapper {
      flex-direction: column;
      margin-left: 2.5rem;
    }
    .timeInterval {
      display: flex;
      justify-content: space-evenly;
      padding: 0;
    }
    .reportCard img {
      margin-right: 1rem;
      width: 28%;
      margin-bottom: 0;
    }
    .reportCard h1 {
      font-size: 1.3rem;
      font-weight: normal;
    }
  }